    groupBy: function groupBy(arr) {
      var map = {},
          dest = [];

      for (var i = 0; i < arr.length; i++) {
        var ai = arr[i];

        if (!map[ai.date]) {
          dest.push({
            date: ai.date,
            listInfo: [ai]
          });
          map[ai.date] = ai;
        } else {
          for (var j = 0; j < dest.length; j++) {
            var dj = dest[j];

            if (dj.date == ai.date) {
              dj.listInfo.push(ai);
              break;
            }
          }
        }
      }

      return dest;
    },
