    forMatMsg: function forMatMsg(str) {
      var arr = str.split("emoji");
      var html = "";

      if (arr.length > 0) {
        arr.forEach(function (a) {
          if (a != "") {
            if (a.indexOf(":") === 0 && a.lastIndexOf(":") === a.length - 1) {
              a = $.emojiarea.createIcon($.emojiarea.icons[a]);
            }

            html += "<span>".concat(a, "</span>");
          }
        });
      } else {
        html = "<span>".concat(str, "</span>");
      }

      return html;
    },
